   .shadow-effect {
        background: #fff;
        padding: 20px;
        border-radius: 4px;
        text-align: center;
    border:1px solid #ECECEC;
        box-shadow: 0 19px 38px rgba(0,0,0,0.10), 0 15px 12px rgba(0,0,0,0.02);
    }
    #testimonials-list .shadow-effect p {
        font-family: inherit;
        font-size: 17px;
        line-height: 1.5;
        margin: 0 0 17px 0;
        font-weight: 300;
    }
    .testimonial-name {
        margin: -17px auto 0;
        display: table;
        width: auto;
        background: #3190E7;
        padding: 9px 35px;
        border-radius: 12px;
        text-align: center;
        color: #fff;
        box-shadow: 0 9px 18px rgba(0,0,0,0.12), 0 5px 7px rgba(0,0,0,0.05);
    }
    #testimonials-list .item {
        text-align: center;
        padding: 50px;
    margin-bottom:80px;
        opacity: .2;
        -webkit-transform: scale3d(0.8, 0.8, 1);
        transform: scale3d(0.8, 0.8, 1);
        transition: all 0.3s ease-in-out;
    }
    #testimonials-list .owl-item.active.center .item {
        opacity: 1;
        -webkit-transform: scale3d(1.0, 1.0, 1);
        transform: scale3d(1.0, 1.0, 1);
    }
    .owl-carousel .owl-item img {
        -webkit-transform-style: preserve-3d;
                transform-style: preserve-3d;
    max-width: 90px;
    border-radius: 50%;
        margin: 0 auto 17px;
    }
    #testimonials-list.owl-carousel .owl-dots .owl-dot.active span,
   #testimonials-list.owl-carousel .owl-dots .owl-dot:hover span {
        background: #3190E7;
        -webkit-transform: translate3d(0px, -50%, 0px) scale(0.7);
                transform: translate3d(0px, -50%, 0px) scale(0.7);
    }
   #testimonials-list.owl-carousel .owl-dots{
    display: inline-block;
    width: 100%;
    text-align: center;
   }
   #testimonials-list.owl-carousel .owl-dots .owl-dot{
    display: inline-block;
   }
    #testimonials-list.owl-carousel .owl-dots .owl-dot span {
        background: #3190E7;
        display: inline-block;
        height: 20px;
        margin: 0 2px 5px;
        -webkit-transform: translate3d(0px, -50%, 0px) scale(0.3);
                transform: translate3d(0px, -50%, 0px) scale(0.3);
        -webkit-transform-origin: 50% 50% 0;
                transform-origin: 50% 50% 0;
        transition: all 250ms ease-out 0s;
        width: 20px;
    }