  .axis {
    margin: 0 1em;
    color: #fff;
    opacity: 0.8;
    cursor: pointer;
    text-transform: uppercase;
  }

  .axis.current {
    opacity: 1;
    position: relative;
  }
  .axis.current:before {
    content: "";
    position: absolute;
    left: -11px;
  }
  .github-corner {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 100;
    opacity: 0.8;
  }
  .github-corner:hover {
    opacity: 1;
  }
  #root {
    z-index: 0;
  }

