.card {
    width: 350px;
    border: none;
    height: 350px;
    border-radius: 3px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
    background-color: var(--background-color-light);
    padding-left: 15px;
    margin-top: 20px;
}

.product-info {
    display: flex;
    justify-content: space-between;
}

.product-price {
    position: absolute;
    right: 10px;
    top: 20px;
}

.product-image img {
    width: 240px;
    transform: rotate(15deg);
    transition: all 0.5s;
}

.product-about {
    position: absolute;
    width: 170px;
    font-size: 10px;
    top: 140px;
    left: -125px;
    opacity: 0;
}

.button {
    position: absolute;
    bottom: -150px;
}

.btn {
    height: 50px;
    font-size: 16px;
    width: 140px !important;
    font-weight: 600;
}

.heart i {
    font-size: 50px;
    opacity: 0;
    color: red;
}

/* hover effect */
.heart i:hover {
    color: red;
}

.card:hover .heart i {
    animation: heart 300ms ease-in forwards;
    animation-delay: 500ms;
}

.card:hover .product-about {
    animation: content 300ms ease-in forwards;
}

.card:hover .product-image img {
    animation: onimage 300ms ease-in forwards;
}

.card:hover .product-price {
    animation: onprice 500ms ease-in forwards;
}

.card:hover .button {
    animation: button 500ms ease-in forwards;
}

/* Animation */
@keyframes content {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
        left: 25px;
    }
}

@keyframes heart {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

@keyframes onprice {
    0% {
        right: 10px;
        top: 20px;
    }

    50% {
        right: 60px;
        top: 30px;
    }

    100% {
        right: 267px;
        top: 70px;
    }
}

@keyframes onimage {
    0% {
        width: 250px;
    }

    50% {
        width: 200px;
        transform: translateY(-30px);
        transform: translateX(100px);
    }

    100% {
        width: 180px;
        top: 100px;
        transform: translateY(100px);
        transform: translateX(110px);
    }
}

@keyframes button {
    0% {
        bottom: -100px;
    }

    100% {
        bottom: 20px;
    }
}


@media only screen and (max-width: 700px) {
    .card {
        width: 300px;
    }

    .button {
        padding: 0;
    }

    .card:hover .product-price {
        animation: normal;
    }

}